import React from 'react'
import { Box, ThemeProvider, Typography } from '@mui/material'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import Filters from '../../../components/Sub/Filters/Filters'
import ExportStudents from '../../../components/Sub/ExportDialogs/ExportStudents/ExportStudents'
import FoundJobPopup from '../../../components/Sub/GlobalPopups/FoundJobPopup/FoundJobPopup'
import EditProfilePopup from '../../../components/Sub/GlobalPopups/EditProfilePopup/EditProfilePopup'
import VacationActionPopup from '../../../components/Sub/GlobalPopups/VacationActionPopup/VacationActionPopup'
import AccelerationPopup from '../../../components/Main/StudentsListPage/AccelerationPopup/AccelerationPopup'
import useStyles from './StatisticsPage.style'
import useStatistics from './useStatistics'
import theme from '../../../utils/themeMUI5'
import { StatisticsCircularProgress, StatisticsPageTrends } from '../../../components/Main/StatisticsPage'

const sortFieldsMapping = {
  id: 'id',
  studentInfo: 'last_name',
  course: 'course_id',
  status: 'status_id',
  start_acceleration_date: 'created_at',
}

const StatItem = ({ title, value, color }) => {
  const Circle = () => <Box sx={{ width: 8, height: 8, borderRadius: '50%', backgroundColor: color }} />
  return (
    <Box sx={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
      <Circle />
      <Typography sx={{ fontWeight: '500' }} variant="body">{`${value}%`}</Typography>
      <Typography sx={{ width: '170px' }} variant="body">
        {title}
      </Typography>
    </Box>
  )
}

const StatisticsPage = ({ setSnack }) => {
  const {
    editOpenState,
    exportOpenState,
    jobOpenState,
    vacationOpenState,
    isLoading,
    studentToEdit,
    filtersJSON,
    filtersHook,
    columns,
    students,
    handleDownload,
    setIsSuccessVacation,
    accelerationOpenState,
    accelerationPopupData,
    rowsCount,
    statistics,
    handleCellClick,
  } = useStatistics()

  const classes = useStyles()

  return (
    <ThemeProvider theme={theme}>
      <main className={classes.root}>
        <StatisticsPageTrends statistics={statistics} />
        <FoundJobPopup openState={jobOpenState} setSnack={setSnack} studentToEdit={studentToEdit} />
        <VacationActionPopup
          openState={vacationOpenState}
          setSnack={setSnack}
          studentToEdit={studentToEdit}
          setSuccessPatch={setIsSuccessVacation}
        />
        <ExportStudents openState={exportOpenState} directory={filtersJSON} />
        <EditProfilePopup openState={editOpenState} setSnack={setSnack} studentToEdit={studentToEdit} />
        <AccelerationPopup
          openState={accelerationOpenState}
          setSnack={setSnack}
          studentToEdit={studentToEdit}
          popupData={accelerationPopupData}
        />
        <Filters filterType="students2" hook={filtersHook} filtersJSON={filtersJSON} isStatistics />
        <Box className={classes.contentBox}>
          <Box className={classes.percentageBox}>
            <StatisticsCircularProgress
              value={76}
              title="% &nbsp; (&nbsp;&nbsp;)"
            />
            <Box className={classes.statisticsAbout}>
              <StatItem title=" " value={35} color="hsla(140, 40%, 66%, 1)" />
              <StatItem title=" " value={21} color="hsla(33, 100%, 79%, 1)" />
              <StatItem title=" " value={12} color="hsla(253, 54%, 61%, 1)" />
              <StatItem title=" " value={10} color="hsla(302, 100%, 87%, 0.87)" />
            </Box>
          </Box>
          <DataTable
            sx={{
              '& .MuiDataGrid-cell': { cursor: 'pointer' },
              '& .MuiDataGrid-columnHeaderTitleContainer': {
                justifyContent: 'center',
              },
              width: '100%',
              overflowX: 'none',
              overflowY: 'scroll !important',
              scrollbarWidth: 'thin',
              padding: '0 0 36px 24px',
              scrollbarColor: 'rgba(26, 27, 34, 0.2) #ffffff',
              '&::-webkit-scrollbar': {
                width: 4,
                // marginRight: 3,
              },
              '&::-webkit-scrollbar-thumb': {
                backgroundColor: 'rgba(26, 27, 34, 0.2)',
              },
            }}
            onCellClick={handleCellClick}
            columns={columns}
            rows={students}
            loading={isLoading}
            rowsCount={rowsCount}
            filtersHook={filtersHook}
            sortFieldsMapping={sortFieldsMapping}
          />
        </Box>
      </main>
    </ThemeProvider>
  )
}

export default React.memo(StatisticsPage)
